








































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			newlist: [],
			recomlist: [],
			classlist:[]
		}
	},
	onLoad() {
		this.static=ut.static;
		this.req_class();
		this.req_new();
		this.req_recom();
	},
	methods: {
		go_home_server(_id){
			console.log(_id)
			wx.navigateTo({
				url: '../home/server?_id='+_id
			})
		},
		go_home_serverinf(_id){
			wx.navigateTo({
				url: '../home/serverinf?_id='+_id
			})
		},
		go_home_reform(){
			wx.navigateTo({
				url: '../home/reform'
			})
		},
		go_home_reform1(){
			wx.navigateTo({
				url: '../home/reform1'
			})
		},
		call(){
			ut.call();
		},
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/class"
			}).then(data=>{
				this.classlist=data;
			})
		},
		req_new(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/newlist"
			}).then(data=>{
				this.newlist=data
			})
		},
		req_recom(){
			ut.request({
				data: {
					parentid:0
				},
				url: "service/recommendlist"
			}).then(data=>{
				this.recomlist=data
			})
		}
	}
}



